*{margin:0;padding:0;box-sizing:border-box}body{position:relative;min-width:385px;width:100%;height:100%;background:#fff;color:#1d1d1d;font-family:"Roboto Condensed",sans-serif;font-size:16px}#root{height:100%}.wrapper{max-width:1280px;width:100%;margin:0 auto;padding:0 20px}.title{margin-bottom:20px;font-size:22px;font-weight:700}.title.title_max{font-size:40px}.title.title_center{text-align:center}a{color:#1d1d1d;text-decoration:none;transition:.3s}a:hover{color:#4e5685;opacity:.8}.mb_10{margin-bottom:10px}.mt_40{margin-top:40px}.mb_40{margin-bottom:40px}
.App{height:100%}
.Header{padding:5px 0;background-color:#eee}.Header__wrap{display:flex;justify-content:space-between;align-items:center}
.Logo{display:flex;align-items:center}.Logo__link{display:block;position:relative;left:-12px;width:100px;height:85px}.Logo__link svg,.Logo__link img{display:block;width:100%;height:100%}.Logo__desc{font-size:20px}
.form__input{display:block;width:100%;padding:5px 10px 5px 0;border:0;border-bottom:1px solid #c1c1c1;background-color:transparent;font-size:16px;font-family:"Roboto Condensed",sans-serif}.form__input:focus,.form__input:focus-visible,.form__select:focus,.form__select:focus-visible,.input__radio:focus,.input__radio:focus-visible,.form__textarea:focus,.form__textarea:focus-visible{outline:0;border-color:#2fbbf4}
.SearchForm{display:flex}.SearchForm .form__input{padding-left:0}.SearchForm__btn-wrap{position:relative;width:35px;height:35px}.SearchForm__btn-wrap input{display:block;position:absolute;top:0;left:0;width:100%;height:100%;opacity:0;cursor:pointer}.SearchForm__btn-wrap svg{display:block;width:100%;height:100%;cursor:pointer}
.Nav__link:not(:last-of-type){margin-right:20px}
.Footer{padding:20px 0 10px;background-color:#eee}.Footer__wrap{display:flex;justify-content:space-between;align-items:center}
.Social__wrap{display:flex}.Social__link{display:block;width:50px;height:50px;margin:0 5px}
.Copyright{max-width:180px;font-size:13px;color:#c1c1c1}.Copyright a{color:#c1c1c1}
.btn{display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:5px 10px;border:0;border-radius:3px;background-color:#2fbbf4;color:#1d1d1d;font-size:18px;cursor:pointer;transition:.3s}.btn:hover{background-color:#2ff489;opacity:1;opacity:initial}.btn:focus{outline:none}
.card__slider{position:relative;overflow:hidden;margin-bottom:60px}.card__list{position:relative;overflow:hidden;display:grid;grid-gap:20px 20px;height:280px;margin-bottom:20px;justify-items:center}.card__item{display:grid;position:absolute;height:280px;max-width:240px;width:100%;align-items:end;padding:40px 20px;border-radius:10px;background-color:#eee;text-align:center;z-index:5;opacity:0;visibility:hidden;transition:.3s}.card__item.js_active{opacity:1;visibility:visible;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.card__word{margin-bottom:5px;font-size:25px;font-weight:bold;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.card__transcription{color:#8e8e8e;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.card__btn-wrap{position:relative;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.card__btn-wrap .btn{position:relative;width:100%;margin:0 auto;z-index:3}.card__btn-wrap .btn.js_hidden{opacity:0;z-index:-1}.card__translation{display:block;position:absolute;width:100%;top:7px;left:0;z-index:0;text-align:center}.card__arrows{display:block;position:absolute;width:100%;top:120px}.card__arrows button{position:absolute;top:0}.card__arrows button.js_hidden{display:none}.card__arrows button:before{content:"";display:block;position:relative;width:24px;height:24px;background:url(/language-cards/static/media/arrow.a1115d7c.svg)}.card__arrows button:first-of-type{right:calc(50% + 200px)}.card__arrows button:first-of-type:before{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.card__arrows button:last-of-type{left:calc(50% + 200px)}.card__counter{font-size:20px;font-weight:bold;text-align:center}
.Word__list{margin-bottom:60px}.Word__list-head{display:grid;grid-template-columns:1fr 1fr 1fr 200px;padding:10px;background-color:#eee;font-weight:bold}.Word__list-head div:last-of-type{text-align:end}.Word__item{display:grid;grid-template-columns:1fr 200px;padding:10px}.Word__item:nth-of-type(even){background-color:#eee}.Word__wrap{display:grid;grid-template-columns:repeat(3, 1fr);align-items:center}.Word__input-wrap{position:relative;margin-right:20px}.Word__input-wrap input{z-index:2;padding:5px;border:1px solid #eee;background-color:#fff}.Word__input-wrap span{display:block;padding:5px;border:1px solid transparent}.Word__edit-items{display:flex;align-items:center;justify-content:flex-end}
.btn_edit{display:flex;align-items:center;margin:0 0 0 5px;font-size:14px}.btn_edit svg{width:20px;height:20px;margin-left:3px}.btn_edit svg path{fill:#1d1d1d !important}
